import  { useState } from 'react'
import ReactFlagsSelect from 'react-flags-select';
import {BiGlobe} from 'react-icons/bi'
import {FiUserPlus, FiUser} from 'react-icons/fi'

export default function Header() {
  const [selected, setSelect] = useState("TR");


  return (
   
    <div className="bg-brand-color ">
    <div className=" containerleft h-11 flex items-center justify-between">
    <a href='#'>
    <img  src="data:image/svg+xml;base64,"/>
    </a>
    <nav className="  flex  text-sm  gap-x-8 ml-8">
      <a href="#" className=" text-white text-xs text-opacity-80 flex items-center  transition hover:text-opacity-100">
      <ReactFlagsSelect
      countries={["US", "GB","TR", "FR", "DE", "IT"]}
      customLabels={{ US: "EN(US)", GB: "EN(GB)",TR:"Türkçe (TR)", FR: "FR", DE: "DE", IT: "IT" }}
      placeholder="Dili Seçiniz (TR)"
      onSelect={code => setSelect(code)}
      selected={selected}
      className="flag-language"
      /></a>  
      <a href="#" className=" text-white  text-opacity-80 flex items-center gap-1 text-xs transition hover:text-opacity-100" ><FiUser size={18}/>Giriş Yap</a>  
      <a href="#" className=" text-white  text-opacity-80 flex items-center gap-1 text-xs transition hover:text-opacity-100" ><FiUserPlus size={18}/>Kayıt Ol</a>  
    </nav>
    </div>
    </div>
  )
}
